#features {
    *, * * {
        font-family: Montserrat;
    }
    h1 { 
        margin-bottom: 2rem;
    }

    h4 {
        font-family: 'MontserratExtraLight';
        font-size: 1.4rem;
        line-height: 1.8rem;
        margin-top: 1rem;
        text-align: center;

        // @media screen and (min-width: 576px) {
        //     text-align: left;
        //     font-size: 2rem;
        //     line-height: 2.8rem;
        // }

        @media screen and (min-width: 992px) {
            text-align: left;
            font-size: 2rem;
            line-height: 2.8rem;
        }
    }

    .marginTop4 {
        margin-top: 4rem;
    }
    .marginTop2 {
        margin-top: 2rem;
    }

    .break {
        flex-basis: 100%;
        height: 0;
    }
    
    /* Use a collapsed column to break to a new column */
    .break-column {
        flex-basis: 100%;
        width: 0;
    }

    .featureHolder {
        width: 100%;
        position: relative;
        display: flex;
        align-self: center;
        justify-content: center;
        margin: 0rem;
        min-height: 10rem;
        flex-wrap: wrap;

        @media screen and (min-width: 424px) {
            margin: 2rem 2rem;
        }

        .hydraeniumServer {
            background-color: rgb(148, 230, 255);
            padding: 10px;
            border: 4px solid #000;
            display: inline-block;
            font-size: 44px;
            position: absolute;
            border-radius: 15px;
            font-family: 'Montserrat';
            color: #000;
        }

        .autoScaling {
            overflow: visible;

            &#one { animation: 20s linear 0s infinite running one }
            &#two { animation: 20s linear 0s infinite running two; }

            @keyframes one {
                0% , 20%, 100% { margin-left: 0px; }
                22%, 98% { margin-left: 200px; }
            }

            @keyframes two {
                0% , 40%, 88%, 100% { margin-right: 0px; }
                42%, 85% { margin-right: 200px; }
            }
        }

        .fileSystem {
            position: inherit;
            display: flex;
            padding: 10px;
            border: 3px solid #000;
            border-radius: 10px;
            font-size: 36px;

            &.server {
                margin-bottom: 1.5rem;
                margin-left: 1rem;
                margin-right: 1rem;
            
                &::after {
                    width: 2px;
                    height: 30px;
                    background-color: red;
                    content: "";
                    position: absolute;
                    bottom: -30px;
                    left: 50%;
                    margin-left: -1px;
                }
                &::before {
                    width: 2px;
                    height: 28px;
                    background-color: red;
                    content: "";
                    position: absolute;
                    top: -28px;
                    left: 50%;
                    margin-left: -1px;
                }
            }

            &#lB, &#file {
                flex-basis: 80%;
                font-size: 1.2rem;
                border-radius: 10px;
                background-color: rgb(15, 150, 0);
                color: white;
                justify-content: center;
            }

            &#lB {
                margin-bottom: 1.5rem;
                margin-left: 1rem;
                margin-right: 1rem;
                background-color: rgb(0, 84, 187);
            }
        }

        .cloudAgnostic {
            position: inherit;

            &#aws, &#azure {
                display: flex;
                flex-basis: 40%;
                height: 8rem;
                font-size: 1.2rem;
                border-radius: 10px;
                border: 2px solid #000;
                background-color: rgb(15, 150, 0);
                color: white;
                justify-content: center;
            }

            &.server {
                position: absolute;
                top: 3rem;
                padding: 10px;
                border: 2px solid #000;
                border-radius: 8px;
                font-size: 28px;
                left: calc(30% - 33px - 1rem);

                &::after {
                    width: 2px;
                    height: 40px;
                    background-color: red;
                    content: "";
                    position: absolute;
                    bottom: -40px;
                    left: 50%;
                    margin-left: -1px;
                }

                &#one { animation: 30s linear 0s infinite running three }
                &#two { animation: 30s linear 0s infinite running four }

                @keyframes three {
                    0%, 10%, 47%, 80%, 100% {left: calc(30% - 33px - 1rem); }
                    12%, 45%, 82%, 98% { left: calc(70% - 33px + 1rem); }
                }

                @keyframes four {
                    0%, 10%, 47%, 100% {left: calc(30% - 33px - 1rem); }
                    12%, 45% { left: calc(70% - 33px + 1rem); }
                }
            }

            &#file {
                flex-basis: 80%;
                margin-top: 1.5rem;
                font-size: 1.2rem;
                border-radius: 10px;
                background-color: rgb(15, 150, 0);
                color: white;
            }

            &#azure {margin-left: 1rem; background-color: #90cbed; color: #000; }
            &#aws {margin-right: 1rem; background-color: #ffc97d; color: #000}
        }

        &#featureK8S {
            img {
                width: 100%;
            }
        }
    }
}